.van-toast {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: content-box;
  color: #fff;
  color: var(--toast-text-color, #fff);
  font-size: 0.59733rem;
  font-size: var(--toast-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--toast-line-height, 0.85333rem);
  white-space: pre-wrap;
  word-wrap: break-word;
  background-color: rgba(0, 0, 0, 0.7);
  background-color: var(--toast-background-color, rgba(0, 0, 0, 0.7));
  border-radius: 0.34133rem;
  border-radius: var(--toast-border-radius, 0.34133rem);
}
.van-toast__container {
  position: fixed;
  top: 50%;
  left: 50%;
  width: -webkit-fit-content;
  width: fit-content;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 70%;
  max-width: var(--toast-max-width, 70%);
}
.van-toast--text {
  min-width: 4.096rem;
  min-width: var(--toast-text-min-width, 4.096rem);
  padding: 0.34133rem 0.512rem;
  padding: var(--toast-text-padding, 0.34133rem 0.512rem);
}
.van-toast--icon {
  width: 3.75467rem;
  width: var(--toast-default-width, 3.75467rem);
  min-height: 3.75467rem;
  min-height: var(--toast-default-min-height, 3.75467rem);
  padding: 0.68267rem;
  padding: var(--toast-default-padding, 0.68267rem);
}
.van-toast--icon .van-toast__icon {
  font-size: 1.536rem;
  font-size: var(--toast-icon-size, 1.536rem);
}
.van-toast--icon .van-toast__text {
  padding-top: 0.34133rem;
}
.van-toast__box {
  display: grid;
  text-align: center;
}
.van-toast__loading {
  margin: 0.42667rem 0;
}
.van-toast--top {
  -webkit-transform: translate(0, -30vh);
          transform: translate(0, -30vh);
}
.van-toast--bottom {
  -webkit-transform: translate(0, 30vh);
          transform: translate(0, 30vh);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
